.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.AccuWeather-logo {
  width: 120px;
}

.App-header,
.App-footer {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

tr.day {
  background-color: #fff;
}

tr.night {
  background-color: #222;
}

tr.twy {
  background-color: red;
}

tr.sunset {
  background-image: -webkit-gradient(linear, left top, left bottom, from(#fff), to(#222));
  background-image: -webkit-linear-gradient(top, #fff, #222);
  background-image: -o-linear-gradient(top, #fff, #222);
  background-image: linear-gradient(to bottom, #fff, #222);
}

tr.sunrise {
  background-image: -webkit-gradient(linear, left top, left bottom, from(#222), to(#fff));
  background-image: -webkit-linear-gradient(top, #222, #fff);
  background-image: -o-linear-gradient(top, #222, #fff);
  background-image: linear-gradient(to bottom, #222, #fff);
}

tr[class="night"]::first-of-type td {
  color: red;
  background-color: orange !important;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}




